<template>
  <div>
    <el-dialog :visible.sync="visible" :modal-append-to-body="false" :before-close="beforeClose" title="添加部门" width="500px">
      <div class="p15">
        <el-form :show-message=true :inline-message=true ref="form" :model="form" :rules="rules" class="form-text f13 " label-width="108px" size="mini">
          <el-row>
            <el-col :span="20">
              <el-form-item label="部门名称：" prop="department_name">
                <el-input
                  placeholder='请输入部门名称'
                  v-model='form.department_name'>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="20">
              <el-form-item label="部门编码：" prop="department_code">
                <el-input
                  placeholder='请输入部门编码'
                  v-model='form.department_code'>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="20">
              <el-form-item class='is-required' label="上级部门：" prop="departmentId">
                <el-input
                  disabled
                  placeholder='无上级部门'
                  v-model="selectDepartment.department_name || '无上级部门'">
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="24">
              <el-form-item label="部门负责人：" prop="duty_manager_ids">
                <el-col :span="24"  style="position:relative; top: -4px">
                  <el-button type="primary" size="mini">添加</el-button>
                </el-col>
                <el-col :span="24">
                  <el-tag
                    v-for="(tag, index) in form.duty_manager_ids"
                    :key="tag"
                    size="small"
                    @close="deleteManager(tag, index)"
                    closable>
                    {{tag}}
                  </el-tag>
                </el-col>
                <!--<el-input-->
                  <!--placeholder='请选择部门负责人'-->
                  <!--v-model='form.dutyManagerNames'>-->
                <!--</el-input>-->
              </el-form-item>
            </el-col>
          </el-row>
          <el-row style="margin-top: 11px">
            <el-col :span="20">
              <el-form-item label="资产管理人：" prop="asset_manager_id">
                <el-input
                  placeholder='请选择资产管理人'
                  v-model='form.asset_manager_id'>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="20">
              <el-form-item label="状态：">
                <el-radio-group v-model="form.status">
                  <el-radio :label="1">启用</el-radio>
                  <el-radio :label="0">禁用</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer mt10" style="text-align: center">
        <el-button type="primary" @click="closeDialog('save')">保 存</el-button>
        <el-button @click="closeDialog('close')">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import { operateDepartment } from '../constants'

  export default {
    name: 'Dialog',
    props: ['dialogFormVisible', 'selectDepartment'],
    computed: {
      visible: {
        get: function () {
          return this.dialogFormVisible
        },
        set: function () {
        }
      },
      rules() {
        return operateDepartment.rules
      }
    },
    data() {
      return {
        form: operateDepartment.init(),
        showModal: false,
        departmentId: ''
      }
    },
    methods: {
      deleteManager(tag, index) {
        console.log(tag)
        console.log(this.form)
      },
      closeDialog(method) {
        if (method !== 'close') {
          this.$refs['form'].validate((valid) => {
            if (valid) {
              this.$emit('closeDialog', {...this.form, parent_dept_id: this.selectDepartment['department_id']}, method)
              this.form = operateDepartment.init()
              this.$refs['form'].resetFields()
            } else {
              return false
            }
          })
        } else {
          this.$emit('closeDialog', {}, 'close')
        }
      },
      beforeClose(done) {
        done()
        this.$emit('closeDialog', {}, 'close')
      }
    },
    // watch: {
    //   selectDepartment() {
    //     if (this.selectDepartment['department_id']) {
    //       this.departmentId = this.selectDepartment['department_id']
    //     }
    //   }
    // }
  }
</script>
<style lang="scss">
  .dialog_input_custom{
    >input{
      height: 28px;
      font-size: 12px;
      top: 1px;
      position: relative;
    }
  }
  .dialog_date_custom{
    .el-input__prefix{
      top: -6px;
    }
  }
</style>


