const ruleCollections = {
  phone: () => {
  	 return [
      { required: true, message: '手机号必填'},
      { validator: validatePhone, trigger: 'blur'},
      { validator: validateNumber}
    ]
		},
  required: (text) => {
   	return  [
      { required: true, message: text, trigger: 'blur'},
    ]
		},
		length: (min, max) => {
  	if (max) {
     return [
       { min, max, message: `位数限制，请输入${min}-${max}位` }
     ]
			}
  	return [
     { validator: validateLength, trigger: 'blur'},
			]
		},
  email: () => {
    return [
      { type: 'email', message: `请填写正确邮箱格式` }
    ]
		},
		date: () => {
    return [
      { required: true, message: '日期必填'},
      { validator: validateDate},
    ]
		}
}

