function validateDate(rule,value,callback) {
  const result = Number(value)
  if (isNaN(result)) {
    callback(new Error(`日期必选`))
  } else{
    callback()
  }
}

function validatePhone(rule,value,callback) {
	 const length = value.length
		if (length !== 11) {
    callback(new Error(`输入11位手机号`))
		} else{
	 	 callback()
		}
}

function validateNumber(rule,value,callback) {
  const result = Number(value)
  if (isNaN(result)) {
    callback(new Error(`必须为纯数字`))
  } else{
    callback()
  }
}

function validateLength(rule,value,callback) {
		if (min) {
    if (!value) {
      callback()
    } else if ( value.toString().length !== min ){
      callback(new Error(`限制${min}位`))
    }
		}
}

